/*
Component Name : Amexio Text Input
Component Selector :  <amexio-text-input>
Component Description : Text input component has been created with
different configurable attributes for validation (min/max length, allow
blank, custom regex), custom error message, help, custom styles.
*/
import { Component, EventEmitter, forwardRef, Input, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, NgModel, Validators } from '@angular/forms';
import { ValueAccessorBase } from '../../base/value-accessor';
@Component({
  selector: 'amexio-text-input',
  template: `
    <div class="inputgroup">

        <label *ngIf="haslabel" [style.font-style]="fontstyle" [style.font-family]="fontfamily" [style.font-size]="fontsize">
          {{fieldlabel}}
      </label>
        <input type="text" #inp="ngModel"  class="input-control" [ngClass]="inp.touched ? (isValid && inp.touched) ? 'input-control-success' : 'input-control-error' :''" [(ngModel)]="value" (blur)="onBlurEvent()" (focus)="onFocusEvent()" (input)="onInput()" (change)="onChangeEv()"
            [name]="name" [pattern]="regEx" [attr.placeholder]="placeholder" [maxlength]="maxlength" [minlength]="minlength" [attr.disabled]="disabled ? true: null" [required]="!allowblank" />


        <ng-container *ngIf="iconfeedback">
            <span class="input-control-feedback">
              <span *ngIf="!isValid && inp.touched"><em class="fa fa-times"></em></span>
            <span *ngIf="isValid && inp.touched"><em class="fa fa-check"></em></span>
            </span>
        </ng-container>


        <span *ngIf="showToolTip && enablepopover" class="dropdown tooltiptext">
          <div [innerHTML]="helpInfoMsg"></div>
      </span>
        <span class="inputfieldbar"></span>
    </div>
  `,
  providers: [{
    provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => AmexioTextInputComponent), multi: true,
  }, {
    provide: NG_VALIDATORS, useExisting: forwardRef(() => AmexioTextInputComponent), multi: true,
  }],
  encapsulation: ViewEncapsulation.None,
})

export class AmexioTextInputComponent extends ValueAccessorBase<string> implements OnInit, Validators {

  /*
   Properties
   name : field-label
   datatype : string
   version : 4.0 onwards
   default :
   description : The label of this field
   */
  @Input('field-label') fieldlabel: string;
  /*
Properties
name : min-length
datatype : number
version : 4.0 onwards
default :
description : Minimum length required for textfield
*/
  @Input('min-length') minlength: number;
  /*
Properties
name : mac-length
datatype : number
version : 4.0 onwards
default :
description : Maximum length required for textfield
*/
  @Input('max-length') maxlength: number;
  /*
Properties
name : allow-blank
datatype : string
version : 4.0 onwards
default :
description : Sets if field is required
*/
  @Input('allow-blank') allowblank: boolean;

  helpInfoMsg: string;

  regEx: RegExp;

  showToolTip: boolean;

  _errormsg: string;

  get errormsg(): string {
    return this._errormsg;
  }
  /*
 Properties
 name : error-msg
 datatype : none
 version : 4.0 onwards
 default : none
 description : Sets the error message
 */
  @Input('error-msg')
  set errormsg(value: string) {
    this.helpInfoMsg = value + '<br/>';
  }

  _minerrormsg: string;

  get minerrormsg(): string {
    return this._minerrormsg;
  }
  /*
 Properties
 name : min-error-msg
 datatype : string
 version : 4.0 onwards
 default :
 description : Sets the error message for min validation
 */
  @Input('min-error-msg')
  set minerrormsg(value: string) {
    this.helpInfoMsg = this.helpInfoMsg + '<b>Min Length<b/>: ' + value + '<br/>';
  }

  _maxerrormsg: string;

  get maxerrormsg(): string {
    return this._maxerrormsg;
  }
  /*
 Properties
 name : max-error-msg
 datatype : string
 version : 4.0 onwards
 default :
 description : Sets the error message for max validation
 */
  @Input('max-error-msg')
  set maxerrormsg(value: string) {
    this.helpInfoMsg = this.helpInfoMsg + 'Max Length: ' + value;
  }
  /*
Properties
name : place-holder
datatype : string
version : 4.0 onwards
default :
description : Show place-holder inside dropdown component
*/
  @Input('place-holder') placeholder: string;
  /*
 Properties
 name : disabled
 datatype : boolean
 version : 4.0 onwards
 default : false
 description : True to disable the field.
 */
  @Input() disabled: boolean;
  /*
Properties
name : icon-feedback
datatype : boolean
version : 4.0 onwards
default :false
description :
*/
  @Input('icon-feedback') iconfeedback = false;
  /*
Properties
name : font-style
datatype : string
version : 4.0 onwards
default :
description : Set font-style to field
*/
  @Input('font-style') fontstyle: string;
  /*
Properties
name : font-family
datatype : string
version : 4.0 onwards
default :
description : Set font-family to field
*/
  @Input('font-family') fontfamily: string;
  /*
Properties
name : font-size
datatype : string
version : 4.0 onwards
default :
description : Set font-size to field
*/
  @Input('font-size') fontsize: string;
  /*
Properties
name : has-label
datatype : boolean
version : 4.0 onwards
default : false
description : Flag to set label
*/
  @Input('has-label') haslabel = true;

  _pattern: string;

  get pattern(): string {
    return this._pattern;
  }
  /*
Properties
name : pattern
datatype : string
version : 4.0 onwards
default :
description : Apply Reg-ex to the field
*/
  @Input('pattern')
  set pattern(value: string) {
    if (value != null) {
      this._pattern = value;
      this.regEx = new RegExp(this._pattern);
    }
  }
  /*
 Properties
 name : enable-popover
 datatype : string
 version : 4.0 onwards
 default :
 description : Set enable / disable popover.
 */
  @Input('enable-popover') enablepopover: boolean;

  isValid = false;

  @ViewChild(NgModel) model: NgModel;

  @Input('name') name: string;

  /*
 Events
 name : onBlur
 datatype : any
 version : 4.0 onwards
 default :
 description : On blur event
 */
  @Output() onBlur: any = new EventEmitter<any>();
  /*
Events
name : input
datatype : any
version : none
default :
description :   On input event field.
*/
  @Output() input: any = new EventEmitter<any>();
  /*
Events
name : focus
datatype : any
version : none
default :
description : On focus event field.
*/
  @Output() focus: any = new EventEmitter<any>();
  /*
Events
name : change
datatype : any
version : none
default :
description : On field value change event
*/
  @Output() change: any = new EventEmitter<any>();
  constructor() {
    super();
    this.showToolTip = false;
  }

  ngOnInit() {
    this.name = this.generateName(this.name, this.fieldlabel, 'textinput');
  }

  // THIS METHOD USED FOR BLUR EVENT.
  onBlurEvent() {
    this.showToolTip = false;
    this.onBlur.emit(this.value);
  }
  // THIS METHOD USED FOR FOCUS EVENT .
  onFocusEvent() {
    this.showToolTip = true;
    this.focus.emit(this.value);
  }
  // THIS METHOD USED FOR  INPUT EVENT .
  onInput() {
    this.isValid = this.isFieldValid();
    this.input.emit(this.value);
  }
  // THIS METHOD USED FOR CHANGE EVENT  .
  onChangeEv() {
    this.change.emit(this.value);
  }

  isFieldValid(): boolean {
    let valid: boolean;
    valid = (!this.allowblank && (this.value && ((this.value.length >= this.minlength) && this.value.length > 0)) ||
      (!this.minlength && this.value && this.value.length > 0)) || this.allowblank;
    return valid;
  }
  public validate(c: FormControl) {
    return this.isFieldValid() ? null : {
      jsonParseError: {
        valid: true,
      },
    };
}
}
