import { Component, Input } from '@angular/core';

@Component({
    selector: 'input-help',
    template: `
      <div class="input-helper">
          {{errormsg}} <br/>
          {{minmsg}} <br/>
          {{maxmsg}} <br/>
      </div>
    `,
    styles: [`
      .input-helper{background-color:#d3d3d3;display:block;z-index:1;overflow:auto;position:absolute;width:100%;max-height:200px;opacity:1;-webkit-box-shadow:0 1px 3px 0 #ced4da;box-shadow:0 1px 3px 0 #ced4da}
    `],
})
export class AmexioInputHelperComponent {
    public _errormsg: string;
    public _minmsg: string;
    public _maxmsg: string;
    public help: string[];
    constructor() {
        this.help = [];
    }

    @Input('error-msg')
    set errormsg(v: string) {
        this._errormsg = v;
    }
    get errormsg() {
        return this._errormsg;
    }

    @Input('min-msg')
    set minmsg(v: string) {
        this._minmsg = v;
    }
    get minmsg() {
        return this._minmsg;
    }

    @Input('max-msg')
    set maxmsg(v: string) {
        this._maxmsg = v;
    }
    get maxmsg() {
        return this._maxmsg;
    }

    private createHelpMsg() {
        if (this.errormsg && this.errormsg.length > 0) {
            this.help.push(this.errormsg);
        }
        if (this.errormsg && this.errormsg.length > 0) {
            this.help.push(this.errormsg);
        }
        if (this.errormsg && this.errormsg.length > 0) {
            this.help.push(this.errormsg);
        }
    }

}
